export default "import React from \"react\";\nimport axios from \"axios\";\n\nconst prefix = /github/.test(window.location.host) ? \"/json-form/build\" : \"\";\n\nexport default [\n  {\n    _component: \"FormInputField\",\n    _name: \"name\",\n    label: \"姓名\",\n    required: \"请输入您的名字\",\n    placeholder: \"请输入名字\"\n  },\n  {\n    _component: \"FormSelectField\",\n    _name: \"province\",\n    _fetch_data: () => axios(`${prefix}/province.json`).then(res => res.data),\n    _format: $component => (\n      <div className=\"tip_item\">\n        <div>{$component}</div>\n        <div>省份数据取自ajax</div>\n      </div>\n    ),\n    label: \"省份\",\n    required: \"请选择您所在省份\",\n    autoWidth: true,\n    data: []\n  },\n  {\n    _component: \"FormSelectField\",\n    _name: \"city\",\n    _show: values => !!values.province,\n    _fetch_data: values =>\n      axios(`${prefix}/${values.province}.json`).then(res => res.data),\n    _format: $component => (\n      <div className=\"tip_item\">\n        <div>{$component}</div>\n        <div>城市数据取自ajax</div>\n      </div>\n    ),\n    _subscribe: (prevValues, values, restart) => {\n      if (values.province !== prevValues.province) {\n        restart();\n      }\n    },\n    label: \"城市\",\n    required: \"请选择您所在城市\",\n    autoWidth: true,\n    data: []\n  },\n  {\n    _component: \"FormSelectField\",\n    _name: \"profession\",\n    label: \"职业\",\n    required: \"请选择您的职业\",\n    autoWidth: true,\n    data: [\n      {\n        text: \"程序员\",\n        value: \"programmer\"\n      },\n      {\n        text: \"设计\",\n        value: \"designer\"\n      },\n      {\n        text: \"测试\",\n        value: \"tester\"\n      }\n    ]\n  },\n  {\n    _component: \"FormSelectField\",\n    _name: \"language\",\n    _show: values => values.profession === \"programmer\",\n    label: \"语言\",\n    required: \"请选择语言\",\n    data: [\n      {\n        text: \"Javascript\",\n        value: \"javascript\"\n      },\n      {\n        text: \"Java\",\n        value: \"java\"\n      }\n    ]\n  },\n  {\n    _show: values => values.frameworker === \"react\",\n    _slot: \"react-banner\"\n  },\n  {\n    _component: \"FormSelectField\",\n    _name: \"frameworker\",\n    _show: values => values.language === \"javascript\",\n    label: \"前端框架\",\n    required: \"请选择前端开发框架\",\n    data: [\n      {\n        text: \"React\",\n        value: \"react\"\n      },\n      {\n        text: \"Vue\",\n        value: \"vue\"\n      },\n      {\n        text: \"Angular\",\n        value: \"angular\"\n      }\n    ]\n  },\n  {\n    _component: \"FormSelectField\",\n    _name: \"frameworker\",\n    _show: values => values.language === \"java\",\n    label: \"后端框架\",\n    required: \"请选择后端开发框架\",\n    data: [\n      {\n        text: \"Spring\",\n        value: \"spring\"\n      },\n      {\n        text: \"Hibernate\",\n        value: \"hibernate\"\n      },\n      {\n        text: \"Struts2\",\n        value: \"struts2\"\n      }\n    ]\n  },\n  {\n    _component: \"FormSelectField\",\n    _name: \"design_tool\",\n    _show: values => values.profession === \"designer\",\n    label: \"作图工具\",\n    required: \"请选择作图工具\",\n    data: [\n      {\n        text: \"PS\",\n        value: \"ps\"\n      },\n      {\n        text: \"Sketch\",\n        value: \"sketch\"\n      }\n    ]\n  },\n  {\n    _component: \"FormSelectField\",\n    _name: \"test_tool\",\n    _show: values => values.profession === \"tester\",\n    label: \"测试工具\",\n    required: \"请选择测试工具\",\n    data: [\n      {\n        text: \"Selenium\",\n        value: \"selenium\"\n      },\n      {\n        text: \"AutoRunner\",\n        value: \"autorunner\"\n      }\n    ]\n  }\n];\n";